
const extractURLParams = (url: string): Record<string, string> => {
    const paramsMap: Record<string, string> = {}

    if (url) {
        try {
            const paramUrl = url.split('?').slice(1).join('?')
            if (paramUrl.length) {

                const params = paramUrl.split('&')
                params.reduce<Record<string, string>>((paramMap, param) => {

                    const splits = param.split('=')
                    if (splits.length) {
                        const name = decodeURIComponent(splits.shift()!)
                        paramMap[name] = decodeURIComponent(splits.join('='))
                    }
                    return paramMap
                }, paramsMap)
            }
        } catch (e) {
            console.error('Unable to parse URL parameters: ', url)
        }
    }
    return paramsMap
}

const capitalize = (str: string): string => str.replace(/^\w/, c => c.toUpperCase())

function findLast<T>(array: Array<T>, predicate: (item: T) => boolean): T | null {

    for (let i = array.length - 1; i >= 0; i--) {
        if (predicate(array[i])) {
            return array[i]
        }
    }
    return null
}

function shallowObjEqual<Obj extends Record<PropertyKey, unknown>>(obj1: Obj, obj2: Obj): boolean {

    for (const key in obj1) {
        if (!(key in obj2) || obj1[key] !== obj2[key]) {
            return false
        }
    }
    for (const key in obj2) {
        if (!(key in obj1) || obj2[key] !== obj1[key]) {
            return false
        }
    }
    return true
}

export type FulfilledResult<T> = {
    status: 'fulfilled'
    value: T
}

export type RejectedResult<T> = {
    status: 'rejected'
    reason: any
}

export type WaitResolveResult<T> = FulfilledResult<T> | RejectedResult<T>

const waitResolveAll = function<T>(targets: Array<T | Promise<T>>): Promise<WaitResolveResult<T>[]> {
    return new Promise<WaitResolveResult<T>[]>((resolve, reject) => {

        if (!targets.length) {
            resolve([])
            return
        }
        let numberOfResolved = 0
        const results = new Array(targets.length)
        const promises = targets.map(target => {
            return (target instanceof Promise ? target : Promise.resolve(target))
        }) as Promise<T>[]
        promises.forEach((targetPromise, index) => {
            targetPromise
            .then(value => {
                results[index] = {
                    status: 'fulfilled',
                    value,
                }
            })
            .catch(reason => {
                results[index] = {
                    status: 'rejected',
                    reason,
                }
            })
            .finally(() => {
                numberOfResolved++
                if (numberOfResolved === targets.length) {
                    resolve(results)
                }
            })
        })
    })
}

const startsWithAny = (base: string, prefixes: string[]) => {
    for (const prefix of prefixes) {
        if (base.startsWith(prefix)) {
            return true
        }
    }
    return false
}

const splitWords = (str: string): string => {
    return str
    .trim()
    .split(/(?=[A-Z])/)
    .map(word => word.trim())
    .join(' ')
}

export const utils = {
    extractURLParams,
    capitalize,
    findLast,
    shallowObjEqual,
    waitResolveAll,
    startsWithAny,
    splitWords,
}
