import {AgentDesktopClientAPIImpl} from '../../adc-client-api'

import {loadCrmApi} from './loadExternalApi'


const logPrefix = '@ADAPTER'
const integrationKey = 'zendesk'
const logger = {
    log: (...args: any) => console.log(logPrefix, ...args),
    info: (...args: any) => console.info(logPrefix, ...args),
    warn: (...args: any) => console.warn(logPrefix, ...args),
    error: (...args: any) => console.error(logPrefix, ...args),
}

export const registerZendeskIntegration = () => {
    loadCrmApi.then(() => {
        const zaf = window.ZAFClient.init()
        logger.log('ZAF', zaf)
        if (!zaf) {
            return logger.error('window.ZAFClient is undefined. Please, check and fix the adapter configuration and reload this page.')
        }
        zaf.invoke('resize', {width: '100%', height: '200px'})
        console.log('@@@ WORKING')

        const adApi = new AgentDesktopClientAPIImpl({integrationKey})
        //TODO: Make an integration
    })

}
